import React, { Component } from 'react';
import Navigation from './components/Navigation/Navigation';
import Scroll from './components/ScrollArea/Scroll';
import Footer from './components/Footer/Footer';
import ViewContainer from './views/ViewContainer';
import 'tachyons';
import './App.css';

var scrollable;
var value;


class App extends Component {

  constructor() {
    super();
    this.state = {
      //!Default: 'home'
      route: 'products',
      bgOpacity: { backgroundColor: 'rgba(255, 255, 255, 0.0' }
    };

  }

  /** Method: onScroll
   ** Desc: This method is called from the event requester allways that
   ** the user scroll down the page, it is used basically to keep the nav var fixed
   ** during the scroll
   *? No questions
   *! No orgent tasks
   *  TODO: Refactor
   */
  onScroll = () => {
    try {
      if (this.state.route === 'home') {

        var buttonsdistance = scrollable.scrollTop;
        console.log(scrollable.scrollTop)
        var navheight = document.getElementById('nav-bar').offsetHeight;
        value = scrollable.scrollTop / (buttonsdistance - navheight);

        this.setState({ bgOpacity: { backgroundColor: `rgba(255, 255, 255, ${value}` } });
      }



    } catch (error) {
      console.log('error scroling')
    }
  }

  changeRoute = (route) => {
    this.setState({ route: route })
    if (route === 'home') {
      this.setState({ bgOpacity: { backgroundColor: `rgba(255, 255, 255, 0` } });
    } else {
      this.setState({ bgOpacity: { backgroundColor: `rgba(255, 255, 255, 1` } });
    }

  }

  componentDidMount() {
    scrollable = document.getElementById('scroll-area')
    scrollable.addEventListener('scroll', this.onScroll);
  }

  componentWillUnmount() {
    scrollable.removeEventListener('scroll', this.onScroll);
  }


  render() {

    return (
      <div>
        <Scroll >
          <Navigation bgOpacity={this.state.bgOpacity} changeRoute={this.changeRoute} />
          <ViewContainer changeRoute={this.changeRoute} route={this.state.route} />
          <Footer />
        </Scroll>
      </div>
    );
  }
}

export default App;
