import React from 'react';


const ProductCard = (props) => {
    const {id, name, description, category} = props;
    var path=`/images/products_images/${category}/${id}.png`
    return (
        
        <div className='w5 tc grow bg-light-blue br3 ma3 pa1 dib shadow-5'>
            <div className='bg-white br3 h7'>
                <img className='h5' src={path} alt='Product' />
            </div>
            
            <div className=''>
                <h2>{name}</h2>
                <p>{description}</p>
            </div>
        </div>
    );
}



export default ProductCard;