import React from 'react';
import HomeButton from './HomeButton';

function importAll(r) {
    return r.keys().map(r);
}
const images = importAll(require.context('../../../public/images/icons/', false, /\.(png|jpe?g|svg)$/));


const HomeButtons = (props) => {

    const onClickProducts = () => {
        props.changeRoute('products');
    }

    return (
        <div id='home-buttons' className='p4 pv5-ns bg-white inline-flex justify-center bg-white w-100'>
            <HomeButton description={"Conócenos"} cardImage={images[0]}></HomeButton>
            <HomeButton description={"Productos"} cardImage={images[1]} onClick={onClickProducts}></HomeButton>
            <HomeButton description={"Contáctanos"} cardImage={images[2]}></HomeButton>
        </div>
    );

}

export default HomeButtons;