import React from 'react';
import ProductCard from './ProductCard';
import porductsList from '../Products/productsList.json'

class ProductsContainer extends React.Component {
    
    render() {

        const productsArray = Object.keys(porductsList).map(function(key) {
            return [porductsList[key]][0];
          });

        return (
            <div className='pt5-ns pt4 bg-light-gray'>
                <div className='pa4 flex flex-wrap'>
                    {productsArray.map((product, i) =>
                        <ProductCard
                            key={i}
                            id={product.id}
                            name={product.name}
                            category={product.category}
                            description={product.description}
                        />
                    )}
                </div>
            </div>
        );
    }
}

export default ProductsContainer;